import {useState} from "react";
import {useForm} from "react-hook-form";
import {showAlert} from "../service/AlertServices";
import {checkUsernameExists, saveAccount} from "../api/Accounts";
import bcrypt from "bcryptjs";
import {useNavigate} from "react-router-dom";

function CreateAccount() {
    const {register, handleSubmit, watch, formState: {errors}} = useForm();
    const [status] = useState(true); // Mc nh trng thi l true
    const [confirmPassword, setConfirmPassword] = useState("");
    const nav = useNavigate();

    // X l gi form
    const onSubmit = async (data) => {
        if (data.password !== confirmPassword) {
            showAlert("Mt khu xc nhn khng khp!", "warning");
            return;
        }

        // Kim tra xem username  tn ti cha
        const usernameExists = await checkUsernameExists(data.username);
        if (usernameExists) {
            showAlert("Username  tn ti, vui lng chn tn khc!", "warning");
            return;
        }

        const hashedPassword = await bcrypt.hash(data.password, 6);
        const accountData = {...data, password: hashedPassword, status};

        try {
            await saveAccount(accountData);
            showAlert("To ti khon thnh cng!", "success");
            nav('/account');
        } catch (error) {
            console.error("Li khi to ti khon:", error);
            showAlert("C li xy ra, vui lng th li!", "warning");
        }
    };

    return (
        <div className="">
            <div className="container mt-5">
                <div className="text-start">
                    <button className="btn btn-dark mt-3 mb-2" onClick={() => nav('/account')}>
                        Back
                    </button>
                </div>
                <form onSubmit={handleSubmit(onSubmit)} className="p-4 mt-1 shadow rounded">

                    <div className="mb-3">
                        <label className="fw-bold">Full Name</label>
                        <input className="form-control" {...register("fullname", {required: true})} />
                        {errors.fullname && <small className="text-danger">Vui lng nhp h tn!</small>}
                    </div>

                    <div className="mb-3">
                        <label className="fw-bold">Username</label>
                        <input className="form-control" {...register("username", {required: true})} />
                        {errors.username && <small className="text-danger">Vui lng nhp username!</small>}
                    </div>

                    <div className="mb-3">
                        <label className="fw-bold">Password</label>
                        <input type="password" className="form-control" {...register("password", {required: true})} />
                        {errors.password && <small className="text-danger">Mt khu khng c b trng!</small>}
                    </div>

                    {/* Xc nhn mt khu nm ngoi form */}
                    <div className="mb-3">
                        <label className="fw-bold">Confirm Password</label>
                        <input
                            type="password"
                            className="form-control"
                            value={confirmPassword}
                            onChange={(e) => setConfirmPassword(e.target.value)}
                        />
                    </div>

                    <div className="mb-3">
                        <label className="fw-bold">Role</label>
                        <select className="form-select" {...register("role", {required: true})}>
                            <option value="USER">USER</option>
                            <option value="ADMIN">ADMIN</option>
                        </select>
                        {errors.role && <small className="text-danger">Vui lng chn vai tr!</small>}
                    </div>

                    <button type="submit" className="btn btn-primary w-100 fw-bold">SAVE</button>
                </form>
            </div>
        </div>
    );
}

export default CreateAccount;
