import React, { useState } from "react";
import {
    MainContainer,
    MessageContainer,
    MessageHeader,
    MessageInput,
    MessageList,
    MinChatUiProvider
} from "@minchat/react-chat-ui";

function Chat() {
    const [messages, setMessages] = useState([
        {
            text: "Xin cho! Ti l AI, bn cn ti gip g?",
            user: { id: "ai", name: "AI Assistant" },
        }
    ]);

    const currentUserId = "user";

    const handleSend = async (text) => {
        if (!text.trim()) return;

        const userMessage = {
            text: String(text),  // p kiu chui
            user: { id: currentUserId, name: "Bn" }
        };
        setMessages(prev => [...prev, userMessage]);

        // Gi AI v thm phn hi
        try {
            const aiReply = await getAIResponse(text);
            const aiMessage = {
                text: String(aiReply),  // p kiu chui
                user: { id: "ai", name: "AI Assistant" }
            };
            setMessages(prev => [...prev, aiMessage]);
        } catch (err) {
            setMessages(prev => [...prev, {
                text: "  xy ra li khi x l AI.",
                user: { id: "ai", name: "AI Assistant" }
            }]);
        }
    };

    const getAIResponse = async (inputText) => {
        try {
            if (typeof window !== "undefined" && window.puter && window.puter.ai) {
                const response = await window.puter.ai.chat(inputText);
                return response || "Ti cha hiu bn ni g.";
            } else {
                console.error("Puter.js cha c ti hoc cha khi to ng.");
                return "Khng th kt ni vi AI lc ny.";
            }
        } catch (error) {
            console.error("Li gi API Puter:", error);
            return "Xin li, ti khng th phn hi lc ny.";
        }
    };


    return (
        <MinChatUiProvider theme="#6ea9d7">
            <MainContainer style={{ height: '100vh' }}>
                <MessageContainer>
                    <MessageHeader title="Tr chuyn vi AI (Hugging Face API)" />
                    <MessageList currentUserId={currentUserId} messages={messages} />
                    <MessageInput onSendMessage={handleSend} showSendButton />
                </MessageContainer>
            </MainContainer>
        </MinChatUiProvider>
    );
}

export default Chat;
