import {showAlert} from "../service/AlertServices";

export const getAccounts = async () => {
    const res = await fetch('https://683dc621199a0039e9e6d42d.mockapi.io/accounts');
    if (!res.ok) {
        throw new Error("Failed to fetch accounts");
    }
    return await res.json();
};

export const getAccount = async (id) => {
    try {
        const response = await fetch(`https://683dc621199a0039e9e6d42d.mockapi.io/accounts/${id}`);

        if (!response.ok) {
            throw new Error("Khng th ly thng tin ti khon!");
        }

        return await response.json();
    } catch (error) {
        console.error("Li khi ly ti khon:", error);
        return null;
    }
};


// api/accounts.js
export const changeStatusAPI = async (id, currentStatus) => {
    try {
        const response = await fetch(`https://683dc621199a0039e9e6d42d.mockapi.io/accounts/${id}`, {
            method: 'PUT',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ status: !currentStatus }) // o trng thi
        });

        if (!response.ok) {
            throw new Error('Khng th cp nht trng thi');
        }

        return await response.json();
    } catch (error) {
        console.error('Li khi cp nht trng thi:', error);
    }
};

export const saveAccount = async (account) => {
    try {
        const response = await fetch("https://683dc621199a0039e9e6d42d.mockapi.io/accounts", {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify(account),
        });

        if (response.ok) {
            showAlert("To ti khon thnh cng!", "success");
        } else {
            showAlert("C li xy ra, vui lng th li!", "warning");
        }
    } catch (error) {
        console.error("Li khi to ti khon:", error);
        showAlert("Li khi kt ni ti server!", "danger");
    }
};

export const checkUsernameExists = async (username) => {
    try {
        const response = await fetch("https://683dc621199a0039e9e6d42d.mockapi.io/accounts");
        const accounts = await response.json();
        return accounts.some(acc => acc.username === username);
    } catch (error) {
        console.error("Li khi kim tra username:", error);
        return false;
    }
};

export const deleteAccount = async (id) => {
    try {
        const response = await fetch(`https://683dc621199a0039e9e6d42d.mockapi.io/accounts/${id}`, {
            method: "DELETE",
        });
        if (!response.ok) {
            throw new Error("Khng th xa ti khon!");
        }
        showAlert("Xa ti khon thnh cng!", "success");
        return true;
    } catch (error) {
        console.error("Li khi xa ti khon:", error);
        showAlert("Li khi xa ti khon, vui lng th li!", "danger");
        return false;
    }
};

export const changePassword = async (id, data) => {
    try {
        const response = await fetch(`https://683dc621199a0039e9e6d42d.mockapi.io/accounts/${id}`, {
            method: "PUT",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify(data),
        });

        if (!response.ok) {
            showAlert("Khng th cp nht ti khon!", "warning");
            return null;
        }
        showAlert("Update successful!", "success");
        localStorage.setItem("user", JSON.stringify(data));
        console.log(localStorage.getItem("user"));
        return await response.json();

    } catch (error) {
        console.error("Li khi cp nht ti khon:", error);
        return null;
    }
};

