import React, { useState, useRef, useEffect } from "react";
import {
    MDBListGroup,
    MDBListGroupItem,
    MDBBadge,
    MDBBtn,
    MDBContainer,
    MDBRow,
    MDBCol,
    MDBInput,
    MDBRadio,
    MDBInputGroup
} from "mdb-react-ui-kit";

function Filter() {
    const [inputText, setInputText] = useState("");
    const [output, setOutput] = useState([]);
    const [searchTerm, setSearchTerm] = useState("");
    const [selectedImage, setSelectedImage] = useState(null);
    const [filterType, setFilterType] = useState("");
    const [filterType1, setFilterType1] = useState("");
    const originalInputText = useRef("");
    const [filteredOutput, setFilteredOutput] = useState([]);
    const [buttonDisabled, setButtonDisabled] = useState(true); // State  kim sot trng thi ca nt

    const parseInput = () => {
        let regex;
        if (filterType1 === '=') {
            regex = /([\d.]+)\s*=\s*(.+)/g;
        } else if (filterType1 === '-') {
            regex = /([\d.]+)\s*-\s*(.+)/g;
        }

        const matches = [...inputText.matchAll(regex)];

        const result = matches.map((match) => {
            const product = match[1];
            const price = parseFloat(match[2]);
            return { product, price };
        });

        // Lc kt qu da trn gi v loi filter
        let filteredResult = [];
        if (filterType === '<=') {
            filteredResult = result.filter((item) => item.price <= parseFloat(searchTerm));
        } else if (filterType === '>=') {
            filteredResult = result.filter((item) => item.price >= parseFloat(searchTerm));
        } else if (filterType === '=') {
            filteredResult = result.filter((item) => item.price === parseFloat(searchTerm));
        }

        setFilteredOutput(filteredResult);
        setOutput(filteredResult);
        originalInputText.current = inputText;
    };

    // useEffect  kim tra ni dung ca textarea v cp nht trng thi ca nt
    useEffect(() => {
        if (inputText.trim() === "") {
            setButtonDisabled(true);
        } else {
            setButtonDisabled(false);
        }
    }, [inputText]);

    useEffect(() => {
        setFilterType1('='); // m bo rng mt gi tr mc nh c chn khi component c to
    }, []);

    return (
        <>
            <div className="App">
                <div className="header">
                    <h1>FILTER BY PRICE</h1>
                </div>
                <label htmlFor="inputText" style={{ fontSize: '30px' }}>Hy nhp list ti y:</label><br />
                <textarea
                    id="inputText"
                    cols="30"
                    rows="5"
                    value={inputText}
                    placeholder="Dn list ti y..."
                    onChange={(e) => setInputText(e.target.value)}
                ></textarea>
                <br />

                <div className="gr-radio">
                    <MDBRadio name='radio' checked={filterType1 === '='} label="Phn cch bi du '=' " inline onChange={() => setFilterType1('=')} />
                    <MDBRadio name='radio' label="Phn cch bi du '-' " inline onChange={() => setFilterType1('-')} />
                </div>

                <div className="inputprice mt-3 mb-3">
                    <MDBInput
                        label="Gi a vo...(Triu)"
                        id="formControlLg"
                        type="text"
                        size="lg"
                        value={searchTerm}
                        onChange={(e) => setSearchTerm(e.target.value)}
                    />
                </div>

                <div className="gr-radio">
                    <MDBRadio name='inlineRadio' value='<=' label='Kt qu nh hn/bng gi a vo' inline onChange={() => setFilterType('<=')} />
                    <MDBRadio name='inlineRadio' value='>=' label='Kt qu ln hn/bng gi a vo' inline onChange={() => setFilterType('>=')} />
                    <MDBRadio name='inlineRadio' value='=' label=' Kt qu bng gi a vo' inline onChange={() => setFilterType('=')} />
                </div>

                <br />
                <MDBBtn disabled={buttonDisabled} onClick={parseInput}>Filter</MDBBtn><br /><br />
                <hr />
                <MDBContainer>
                    <MDBRow center>
                        <MDBCol size="4">
                            <div className="result">
                                <strong>Kt qu ( {output.length} s ) :</strong>
                                <MDBListGroup numbered style={{ maxWidth: "26rem" }} light>
                                    {output.map((o, index) => (
                                        <MDBListGroupItem
                                            key={index}
                                            className="d-flex justify-content-between align-items-center"
                                        >
                                            <div className="ms-2 me-auto">
                                                <div className="fw-bold">{o.product}</div>
                                            </div>
                                            <MDBBadge pill light>
                                                {o.price} Triu <br />
                                            </MDBBadge>
                                        </MDBListGroupItem>
                                    ))}
                                </MDBListGroup>
                            </div>
                        </MDBCol>
                        <MDBCol size="4">
                            <strong>Danh sch kt qu</strong>
                            <MDBInputGroup>
                                <textarea
                                    readOnly
                                    className="form-control"
                                    value={output.map((o, i) => `${o.product} = ${o.price} Triu`).join("\n")}
                                ></textarea>
                            </MDBInputGroup>
                        </MDBCol>
                    </MDBRow>
                </MDBContainer>
                <br /><br />
            </div>
        </>
    );
}

export default Filter;
